// src/components/Timetable.js
import React, { useState, useEffect, useContext } from 'react';
import FullCalendar from '@fullcalendar/react';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import axios from 'axios';
import { AuthContext } from '../context/AuthContext';
import BookingForm from './BookingForm'; // Make sure this import is correct
import './Timetable.css';


const Timetable = ({ roomId }) => {
  const { authTokens } = useContext(AuthContext);
  const [events, setEvents] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState(null);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const response = await axios.get(`/api/bookings?roomId=${roomId}`, {
          headers: { Authorization: `Bearer ${authTokens?.access}` }
        });
        setEvents(response.data);
      } catch (error) {
        console.error('Error fetching bookings', error);
      }
    };

    fetchBookings();
  }, [authTokens, roomId]);

  const handleDateSelect = (selectInfo) => {
    setSelectedEvent({
      start: selectInfo.startStr,
      end: selectInfo.endStr,
    });
  };

  return (
    <div className="timetable">
      <FullCalendar
        plugins={[timeGridPlugin, interactionPlugin]}
        initialView="timeGridWeek"
        selectable={true}
        selectMirror={true}
        headerToolbar={{
          left: 'prev,next today',
          center: 'title',
          right: 'timeGridWeek,timeGridDay'
        }}
        events={events}
        select={handleDateSelect}
      />
      {selectedEvent && (
        <BookingForm
          roomId={roomId}
          start={selectedEvent.start}
          end={selectedEvent.end}
          onBookingComplete={() => setSelectedEvent(null)}
        />
      )}
    </div>
  );
};

export default Timetable;
